export default class StatusPage extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        currentPlayersList: null,
        servers: []
      };
      console.log("construcitng status page")

      serversList.forEach(server=>this.state.servers.push({
        error: null,
        isLoaded: false,
        "ip": server[0],
        "name": server[1],
        "playersData": null,
        "serverData": null,
        "lastUpdate": null
      }));

      this.close = (index) =>{
        this.fetchServer(this.state.servers[index],index);
        return;
      }

      this.renderPlayersList = (index) => {
        this.setState({currentPlayersList: index})
      }

      this.fetchServer = (server,index) => {
        //https://cors-anywhere.herokuapp.com/
        fetch("https://cors-anywhere.herokuapp.com/http://"+server.ip+"/status/widget/players.json")
        .then(res => res.json())
        .then(
          (res) => {
            this.setState(state => {
              const {servers} = state;
              servers[index].isLoaded = true;
              servers[index].playersData = res.players;
              servers[index].serverData = res.server;
              servers[index].lastUpdate = Date.now();
              return {servers};
            });
          },
          (error) => {
            this.setState(state => {
              const {servers} = state;
              servers[index].isLoaded = true;
              servers[index].error = error;
              return {servers};
            });
          }
        )
      }


    }

    componentDidMount() {          
      this.state.servers.forEach(this.fetchServer);
      return;
    }

    componentWillUnmount() {
      console.log("componentWillUnmount")
      return;
    }


    render() {
      let playersListElement;

      if(this.state.currentPlayersList !== null){
        if(this.state.servers[this.state.currentPlayersList].playersData.length === 0){

          playersListElement = <div><h2>Server Is Empty</h2></div>

        }else{
          
          playersListElement = (<div>
          <h2>Players List On {this.state.servers[this.state.currentPlayersList].name}<br/>
          IP: <a href ={"fivem://connect/" + this.state.servers[this.state.currentPlayersList].ip}>{this.state.servers[this.state.currentPlayersList].ip}</a></h2>
          
          <table id="playersList">
            <tbody>
            <tr><th>#</th><th>Name</th><th>ID</th><th>Avatar</th></tr>
            
            {this.state.servers[this.state.currentPlayersList].playersData.map((player,index) => (
            <tr key={player[2]}>
              <td>{index+1}</td>
              <td>{player[0]}</td>
              <td>{player[2]}</td>
              <td>{player[3] ? (<img src={player[3] || "#"} height="50px" alt="img"/>) : <div class="no-avatar"/>}</td>
            </tr>
            ))}

            </tbody>
          </table></div>)
          
          
        }

      }

      //================

      return (
      <div>
      <h2>Transport Tycoon Servers List</h2>
      <table id="serversList">
         <tbody>

          {this.state.servers.map((server,index)=>{
          if (server.error) {
              // return <div key={index}><h2>Error: {server.error.message}</h2></div>;
              return (
                <tr key={index}><td>{server.ip}</td><td>{server.name}</td><td>-/-</td><td style={{color: "red"}}>Offline</td><td></td><td></td></tr>
              );

          } else if (!server.isLoaded) {

            return (
              <tr key={index}><td>{server.ip}</td><td>{server.name}</td><td>-/-</td><td style={{color: "grey"}}>Loading</td><td></td><td></td></tr>
            );

          } else {

            return (
              <tr key={index}><td>{server.ip}</td><td>{server.name}</td><td>{server.playersData.length}/{server.serverData.limit}</td><td style={{color: "blue"}}>Online</td><td>{server.lastUpdate}</td><td><input type="button" value="Players" onClick={()=>this.renderPlayersList(index)}/></td></tr>
            );
          }
        })}
        </tbody>
      </table>
        <br/>
        {playersListElement}
      </div>
      )
    }
}