import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect
} from "react-router-dom";
import StatusPage from "./StatusPage.js";
import "./App.css";

export default function App() {
  return (
    <Router basename="ttstats">
    <header>
      <ul>
        <li><Link to="/">Page 1</Link></li>
        <li><Link to="?status">Status</Link></li>
        <li><Link to="?potato">Page 3</Link></li>
      </ul>
      {/* <Route
          path="/blog/:slug?"
          render={({ match }) => {
              {console.log(match)}
              return <div />;
          }}
      /> */}

      {/* <Route
          path="/kebat"
          render={({ match }) => {
              {console.log(match)}
              return <div />;
          }}
      /> */}

    </header>
    <main>
      <Route component={
        (props)=>{
            switch(props.location.search){
                case ("?potato"):
                    // return <MyComponent/>;
                    return <About/>;
                case ("?status"):
                    return <StatusPage/>;
                case (""):
                    return <Home/>;
                default:
                    return <Redirect to="/"/>;
            }
        }}/>
    </main>
    <footer>
      <p>Footer</p>
    </footer>
    </Router>
  );
}

function Home() {
    return (
        
      <div>
        <h2>Page1</h2>
      </div>
    );
  }

function About() {
  return (
      
    <div>
      <h2>Page3</h2>
    </div>
  );
}


